import React, { useState } from 'react'
import { graphql } from 'gatsby'
import moment from 'moment'

import Layout from './../layout'
import { SEO, StripeBar, Banner, Button } from './../components'
import { SubmitEvent, FindEvents, PulseEvent } from './../containers/Pulse'
import { Date as DateComp, Content } from './../containers/Pulse/style'
import CalendarImg from './../images/on-the-pulse-calendar.svg'
import { useLocation } from '@reach/router'
import { format } from 'date-fns'
import frLocale from 'date-fns/locale/fr'

interface IPulsePageProps {
  location: {
    pathname: string
  }
  pageContext: {
    language: string
    title: string
    data: any
  }
}

export default ({
  location,
  pageContext: { language, title, data },
}: IPulsePageProps) => {
  const loc = useLocation()
  const isFr = loc.pathname.startsWith('/fr/')

  return (
    <Layout location={location}>
      <SEO
        pathname={location.pathname}
        title={title}
        keyword={title}
        description={title}
      />
      <Banner colors={['#397897', '#00587F', '#80D0DB']}>
        <h1>{title}</h1>
      </Banner>
      <StripeBar colors={['#E58630', '#DA5027', '#D1932A', '#D16428']} />
      <div className='container'>
        <Content>
          {data.map(({ node: { frontmatter } }, index: number) => {
            return (
              <PulseEvent
                language={language}
                key={`${index}key`}
                {...frontmatter}
              />
            )
          })}
        </Content>
      </div>
    </Layout>
  )
}
