import React from 'react'
import { Link } from 'gatsby'
import ReactHTMLParser from 'react-html-parser'
import { Remarkable } from 'remarkable'
import Truncate from 'react-truncate'
import { useLocation } from '@reach/router'
import { Container, Title, Content, Mark } from './style'
import { TextContent } from './../../../components'
import { format } from 'date-fns'
import frLocale from 'date-fns/locale/fr'
import { composeSlug } from './../../../utils'
interface IPulseEvent {
  language: string
  title: string
  titleFr: string
  eventStartDate: string
  eventEndDate: string
  eventDescription: string
  eventDescriptionFr: string
  eventCost: any
}

export default ({
  language,
  title,
  titleFr,
  eventStartDate,
  eventEndDate,
  eventDescription,
  eventDescriptionFr,
  eventCost,
}: IPulseEvent) => {
  const url = `${language === 'Fr' ? '/fr' : ''}/event/${composeSlug(title)}`
  const md = new Remarkable()
  const loc = useLocation()
  const isFr = loc.pathname.startsWith('/fr/')

  return (
    <Container>
      <Title>
        <div className='position-absolute d-flex w-100 h-100'>
          <Link className='m-auto' to={url}>
            <span>{language === 'Fr' ? titleFr : title}</span>
          </Link>
        </div>
      </Title>
      <Content>
        <h5 className='mb-4 d-flex flex-wrap'>
          <span>
            {format(new Date(eventStartDate), 'MMM d @ h:mm a', {
              locale: isFr ? frLocale : undefined,
            })}
          </span>
          &nbsp;-&nbsp;
          <span>
            {format(new Date(eventEndDate), 'MMM d @ h:mm a', {
              locale: isFr ? frLocale : undefined,
            })}
          </span>{' '}
        </h5>
        <div className='mb-5'>
          <Truncate
            lines={4}
            ellipsis={
              <span>
                <a href={url}>
                  <span>&nbsp;[...]</span>
                </a>
              </span>
            }
          >
            {ReactHTMLParser(
              md.render(
                language === 'Fr' ? eventDescriptionFr : eventDescription
              )
            )}
          </Truncate>
        </div>
        <TextContent>
          <Link className='d-block' to={url}>
            <span>
              {isFr
                ? 'En savoir plus sur cet événement'
                : 'Find out more about this event'}{' '}
              {'>'}{' '}
            </span>
          </Link>
        </TextContent>
        {!eventCost && <Mark>{language === 'Fr' ? 'Gratuit' : 'FREE'}</Mark>}
      </Content>
    </Container>
  )
}
